import {memo} from 'react';

const Card = ({id,name,email}) => {
      return (
      	        <div className="pa2 ma2 br3 bg-light-yellow grow shadow-5">
                    <img alt="robot" src={`https://robohash.org/${id}`} 
                      width="300" height="300"/>
                   	<h2>{name}</h2>
                   	<p>{email}</p>
                 </div>
      	);
}

export default  memo(Card);